.space {
    display: none;
}

.slick-slider {
    height: 80vh;
}


.carousel-one-image {
    background: url('../images/carouselOne.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom right;
    height: 80vh;    
}

.carousel-one {
    overflow-y: hidden;
    color: #fff;
    padding-top: 22vh;
    padding-left: 47vh;
    height: inherit;
}

.carousel-one-title {
    font-size: 40px;
    font-weight: bold;  
}

.carousel-one-title-top {
    font-size: 20px;
    font-weight: initial;
}

.carousel-liner {
    display: block;
    width: 10%;
    margin: 0 auto;
    margin-top: 1em;
    margin-bottom: 1em;
    border-top: 5px solid #fff;
}

/* .carousel-one-subtitle {
    font-size: 25px;
    width: fit-content;
    text-align: center;
} */
.carousel-one-subtitle {
    padding-top: 0.5em;
    font-size: 25px;
}

.carousel-one-subtitle span{
    line-height: 2em;
}

/* Carousel Two css */

.carousel-two-image {
    background: url('../images/carouselTwo.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: left;
    height: 80vh;
    width: 90%;
}

.carousel-two {
    overflow-y: hidden;
    color: #fff;
    padding-top: 22vh;
    padding-left: 47vh;
    height: inherit;
}

.carousel-two-title {
    font-size: 40px;
    font-weight: bold;    
}

.carousel-two-title-top {
    font-size: 20px;
    font-weight: initial;
}

.carousel-two-subtitle {
    font-size: 25px;
    width: fit-content;
}

.carousel-two-subtitle span{
    line-height: 2em;
}

/* Carousel Three Css */

.carousel-three-image {
    background: url('../images/carouselThree.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center left;
    height: 80vh;
    width: 90%;
}

.carousel-three {
    overflow-y: hidden;
    color: #fff;
    padding-top: 22vh;
    padding-left: 47vh;
    height: inherit;
}

.carousel-three-title {
    font-size: 40px;
    font-weight: bold;        
}

.carousel-three-title-top {
    font-size: 20px;
    font-weight: initial;
}

.carousel-three-subtitle {
    font-size: 25px;
    width: fit-content;
}

.carousel-three-subtitle span{
    line-height: 2em;
}

.carousel-three a, .carousel-two a, .carousel-one a {
    display: inline-block;
    color: #fff;
    margin-top: 0.6em;
    padding: 0.2em 1em;
    background-color: #333;
    text-decoration: none;
    /* border: 1px solid #E67E22; */
    cursor: pointer;
}

@media only screen and (max-width: 1440px) {
    .carousel-one-title, .carousel-two-title, .carousel-three-title {
        font-size: 35px;
    }
    .carousel-one-subtitle, .carousel-two-subtitle, .carousel-three-subtitle {
        font-size: 20px;
    }
}


@media only screen and (max-width: 1024px) {    
    .carousel-one, .carousel-two, .carousel-three {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding-left: 0;
    }
    .carousel-one-subtitle p{
        margin-left: 28vh;
    }

    .carousel-one-title, .carousel-two-title, .carousel-three-title, .carousel-three-title {
        margin-left: 0;
        font-size: 35px;
    }
    .carousel-one-subtitle, .carousel-two-subtitle, .carousel-three-subtitle {
        margin-left: 0;
        font-size: 22px;
    }
}

@media only screen and (max-width: 850px) {
    .carousel-one-subtitle, .carousel-two-subtitle, .carousel-three-subtitle {
        font-size: 20px;
    }
    .carousel-one-subtitle p{
        margin-left: 20vh;
    }
}

@media only screen and (max-width: 650px) {
    .carousel-one-title, .carousel-two-title, .carousel-three-title {
        font-size: 22px;
        margin-left: 0;    
    }
    .carousel-one-subtitle p{
        margin-left: 15vh;
    }
    .carousel-one-subtitle, .carousel-two-subtitle, .carousel-three-subtitle {
        font-size: 18px;
        margin-left: 0;
    }
}

@media only screen and (max-width: 550px) {
    .carousel-one-subtitle p{
        margin-left: 5vh;
    }

}

@media only screen and (max-width: 350px) {
    .space {
        display: initial;
    }
}

/* height settings for mobile */